.welcome {
  .wrap();
  .wrap(outside, 15%);
  &__center {
    .col;
    .col(1, 1);
  }

  &__header {
    font-family: 'Marmelad', sans-serif;
    color: @gray-lighter;
    font-size: 5rem;
    text-shadow: @gray-darker 1px 1px 1px;
  }

  &__text {
    font-family: 'Exo 2', sans-serif;
    color: @gray-lighter;
    font-size: 2rem;
    text-shadow: @gray-darker 1px 1px 1px;
  }

  &__link {
    .link();
    padding: 0 0 0 .25rem;
  }
}
