@menu-font-size: 1.1rem;

.menu {
  height: @base-size;
  background: @gray-darker;
  border-bottom: 1px @gray-base solid;
  box-shadow: 0 @base-size / 2 25px rgba(0, 0, 0, 0.7);
  position: relative;
  min-width: @min-width;
}

.menu__layout {
  margin: 0 auto;
  width: 100%;
  max-width: @max-width;
  min-width: @min-width;
}

.menu__container {
  font-family: 'Marmelad', sans-serif;
  padding: @base-size 0;
  font-size: @menu-font-size;
  border: 1px @gray-base solid;
  border-radius: 0 0 @border-radius @border-radius;
  background: @gray-darker;
  position: relative;
  float: right;
  margin: 0;
  box-shadow: 0 @base-size 25px rgba(0, 0, 0, 0.7);
  &:before {
    content: ' ';
    position: absolute;
    top: 0;
    left: 0;
    height: calc(@base-size ~'- 2px');
    width: 102%;
    margin: 0 -1%;
    display: block;
    background: @gray-darker;
  }
}

.menu__item {
  display: inline-block;
  padding: 0 1em;
}

.menu__item-link {
  color: @gray-lighter;
  text-decoration: none;

  &:hover {
    color: @gray-light;
    text-shadow: @gray-dark 1px 0px 3px;
  }
}

.menu__svg {
  fill: @gray-lighter;
  height: @menu-font-size * 1.2;
  width: @menu-font-size * 1.2;
  position: relative;
  top: 3px;
  &:hover {
    fill: @gray-light;
    .svg-shadow(@gray-dark);
  }
}
