@panel-border-thickness: 1px;

.panel {
  .wrap;
  border: @panel-border-thickness @border-color solid;
  border-radius: @border-radius;
  background: fade(@gray, 40%);
  &__title {
    .col;
    border: @panel-border-thickness @border-color solid;
    border-top: none;
    border-left: none;
    border-radius: @border-radius 0 @border-radius 0;
    padding: @base-size @base-size * 4;
    background: fade(@gray, 70%);
  }
  &__body {
    .col;
    .col(1, 1);
    padding: @base-size @base-size * 3 @base-size * 2;
  }
  &__header {
    padding: 0;
    margin: 0;
    line-height: @base-size * 2;
    font-size: @base-size * 3;
    color: lighten(@gray-lighter, 10%);
  }
  &__paragraph {
    text-align: justify;
    margin: 0;
    padding: 0;
  }
  &__link {
    margin-left: @base-size;
  }
  &__svg {
    width: @base-size * 1.5;
    height: @base-size * 1.5;
    .link-svg();
  }
}

.row {
  .wrap;
  .wrap(gutter, 24);
  .wrap(top);
  &__col {
    .col;
    vertical-align: top;
  }
  &__col--two {
    .col(1,2);
  }
  &__col--three {
    .col(1,3);
  }
  &__col--four {
    .col(1,4);
  }
  &__col--bottom-space {
    margin-bottom: @base-size * 1.5;
  }
  &__name {
    display: inline-block;
  }
  &__name--asterix {
    &:before {
      content: '* ';
    }
  }
  &__links {
    //display: inline-block;
  }
  &__link {
    display: inline-block;
    .link();
    padding-right: @base-size;
  }
}

@media (max-width: 768px) {
  .row {
    &__col {
      .col;
      vertical-align: top;
    }
    &__col--two {
      .col(1, 1);
    }
    &__col--three {
      .col(1, 2);
    }
    &__col--four {
      .col(1, 2);
    }
  }
}

