.html {
  height: 100%;
  width: 100%;
}

.body {
  height: 100%;
  width: 100%;
  background: url(/img/irongrip.png) @gray-dark;
  font-size: 16px;
}

@media (max-width: 768px) {
  .body {
    font-size: 14px;
  }
}

@media (max-width: 640px) {
  .body {
    font-size: 12px;
  }
}

.link {
  color: shade(@brand-warning, @effect-proc);
  text-decoration: none;
  &:hover {
    color: @brand-warning;
    text-decoration: underline;
  }
}

.link-svg {
  fill: shade(@brand-warning, @effect-proc);
  &:hover {
    fill: @brand-warning;
  }
}
