.skills {
  .wrap;
  .wrap(gutter, 12);
  .wrap(top);
  &__paragraph {
    .col;
    .col(1, 1);
    text-align: justify;
    margin: 0;
    padding: 0;
  }
}

.area {
  .col;
  .col(1, 3);
  vertical-align: top;
  &__title {
    padding: 0;
    margin: 0;
    line-height: @base-size * 2;
    font-size: @base-size * 2.2;
  }
  &__list {
    padding: 0 @base-size * 3;
    margin: @base-size 0;
    list-style-type: square;
  }
  &__item {

  }
}

@media (max-width: 768px) {
  .area {
    .col;
    .col(1, 2);
  }
}

@media (max-width: 640px) {
  .area {
    .col;
    .col(1, 1);
  }
}

