.first {
  margin-bottom: @base-size;
  &__line {
    .wrap;
    display: block;
  }
  &__name {
    .col;
    .col(1, 5);
    font-weight: bold;
    padding-right: @base-size;
    display: inline-block;
    text-align: right;
    vertical-align: top;
    &:after {
      content: ':';
    }
  }
  &__content {
    .col;
    .col(4, 5);
    text-align: justify;
    display: inline-block;
  }
}
