
@cv-base: 1rem;
@cv-base-px: 10px;

.cv {
  font-family: 'Exo 2', sans-serif;
  width: 99%;
  max-width: @max-width;
  margin: @cv-base auto;
  display: block;
  height: 100%;
  color: @gray-lighter;

  &__section {
    padding: 0 0 @cv-base;
  }
}

.cv-head {
  .wrap;
  &__title {
    .col;
    .col(11, 12);
  }
  &__pdf {
    .col;
    .col(1, 12);
    text-align: right;
  }
  &__svg--download-cv {
    width: @base-size * 4;
    height: @base-size * 4;
    fill: shade(@brand-warning, @effect-proc);
    &:hover {
      fill: @brand-warning;
    }
  }
  &__header {
    font-size: @base-size * 4;
    color: lighten(@gray-lighter, 10%);
    margin: 0;
  }
  &__details {
    padding: 0.1em 0;
    font-size: @base-size * 2.5;
  }
}

.contacts {
  .wrap;
  .wrap(gutter, 24);
  .wrap(outside, 24);
  .wrap(center);

  &__item {
    .col;
    .col(1, 2);
    padding: @cv-base @cv-base / 2;
  }

  &__label {
    display: inline-block;
    padding: 0 @cv-base-px;
  }

  //&__text {
  //  color: @gray-lighter;
  //  display: inline-block;
  //  text-decoration: none;
  //  font-weight: bold;
  //}
}
