.project {
  padding-bottom: @base-size;
  border-bottom: 1px @border-color solid;
  margin-bottom: @base-size;
  &__line {
    .wrap;
    display: block;
  }
  &__name {
    .col;
    .col(1, 5);
    font-weight: bold;
    padding-right: @base-size;
    display: inline-block;
    text-align: right;
    vertical-align: top;
    &:after {
      content: ':';
    }
  }
  &__content {
    .col;
    .col(4, 5);
    text-align: justify;
    display: inline-block;
  }
  &:last-child {
    border: none;
  }
}

@media (max-width: 768px) {
  .project {
    &__line {
      .wrap;
      display: block;
    }
    &__name {
      .col;
      .col(1, 1);
    }
    &__content {
      .col;
      .col(1, 1);
    }
  }
}
