@item-size: 3rem;

.social {
  position: absolute;
  right: 5%;
  background: @gray-darker;
  border: 1px @gray-base solid;
  border-radius: @border-radius;
}

.social__container {
  min-width: @item-size;
  margin: @item-size / 4;
  padding: 0;
}

.social__item {
  display: inline-block;
  padding: 0 @item-size / 8;
}

.social__svg {
  width: @item-size;
  height: @item-size;

  &:hover {
    .svg-shadow(@gray-dark);
  }
}

@effect-proc: 30%;

@twitter-color: #00aced;

.social__svg--twitter {
  fill: shade(@twitter-color, @effect-proc);
  &:hover {
    fill: @twitter-color;

  }
}

@facebook-color: #3b5998;

.social__svg--facebook {
  fill: shade(@facebook-color, @effect-proc);
  &:hover {
    fill: @facebook-color;
  }
}

